<template>
  <div class="nav-main">
    <div class="nav-msg">
      <BaseIcon type="list" @click="toggleMenu"></BaseIcon>
      <span class="msg">{{ msg }}</span>
    </div>
    <div class="nav-dropMenu">
      <AnimList>
        <NavItem v-for="item in menuList" :key="item.id" :name="item.name" :url="item.direct"></NavItem>
      </AnimList>
    </div>
  </div>
</template>

<script>
import animate from "animate.css";
import BaseIcon from "@/components/Icon/BaseIcon";
import NavItem from "@/components/Blog/Nav/NavItem";
import AnimList from "@/components/Anim/AnimList";

export default {
  components: {
    BaseIcon,
    NavItem,
    AnimList
  },
  props: {
    msg: String
  },
  data() {
    return {
      showMenu: false,
      menuList: []
    };
  },
  methods: {
    toggleMenu() {
      this.showMenu = !this.showMenu;
      if (this.showMenu) {
        this.menuList = [
          { id: 1, name: "首页", direct: "/" },
          {
            id: 2,
            name: "登录",
            direct: "/login"
          }
        ];
      } else {
        this.menuList = [];
      }
    },
    reLogin() {
      this.$router.push("/login");
    }
  }
};
</script>

<style scoped>
.nav-main {
  width: 100%;
  margin: 0;
  padding: 12px 20px;
  border: none;
  background: #16a085;
  color: #fff;
}
.nav-msg {
  text-align: left;
}
.nav-dropMenu {
  animation: toggle 1s;
}
.msg {
  margin-left: 10px;
}
.hide {
  display: none;
}
.show {
  display: show;
}
ul {
  margin: 0;
  padding: 0;
}
</style>


