<template>
  <div class="post" @click="enterPost">
    <div>{{ title }}</div>
    <span class="time">{{ time }}</span>
  </div>
</template>

<script>
export default {
  props: {
    id: Number,
    title: String,
    time: String
  },
  methods: {
    enterPost() {
      this.$router.push(
        `/user/${this.$route.params.id}/blog/post/${this.id}`
      );
    }
  }
};
</script>

<style scoped>
.post {
  width: 60%;
  margin: 20px auto;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 0 0 2px #ccc;
  cursor: pointer;
}
.post:hover {
  box-shadow: 0 0 20px #ccc;
}
.time {
  font-size: 12px;
}
</style>


