<template>
  <div class="main">
    <div class="blog-nav">
      <BlogNav :msg="navMsg"></BlogNav>
    </div>
    <div class="blog-main">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import BlogNav from "./Nav/Nav";
export default {
  components: {
    BlogNav
  },
  data() {
    return {
      navMsg: "LuvBlog"
    };
  }
};
</script>

<style scoped>
.main {
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 20px;
  background: #fff;
  color: #34495e;
  font-size: 20px;
}
.blog-nav {
  position: absolute;
  width: 100%;
  z-index:100;
}
.blog-main {
  position: absolute;
  top: 56px;
  width: 100%;
  bottom: 0;
}
</style>


