<template>
  <div class="main">
    <div class="alert" :class="messageType">
      <BaseIcon :type="msgIconType"></BaseIcon>
      {{ message }}
    </div>
    <div class="vertical-align">
      <LoginInput name="User" type="account" @input="account=$event" iconType="child"></LoginInput>
      <LoginInput name="PassWord" type="password" @input="password=$event" iconType="key"></LoginInput>
      <button class="btn btn-success login-btn" @click="submit">{{ btnMsg }}</button>
    </div>
  </div>
</template>

<script>
import BaseIcon from "@/components/Icon/BaseIcon";
import LoginInput from "@/components/Login/Input/LoginInput";
import LoginButton from "@/components/Login/Button/LoginButton";
import validator from "./LoginDB";

export default {
  components: {
    BaseIcon,
    LoginInput,
    LoginButton
  },
  data() {
    return {
      message: "请登录后查看博客内容",
      btnMsg: "Login",
      msgType: "info",
      msgIconType: "info-circle",
      account: "",
      password: ""
    };
  },
  methods: {
    submit() {
      if (this.account == "") {
        this.message = "账号不能为空";
        this.msgType = "warning";
        this.msgIconType = "exclamation-circle";
      } else if (this.password == "") {
        this.message = "密码不能为空";
        this.msgType = "warning";
        this.msgIconType = "exclamation-circle";
      } else if (validator(this.account, this.password)) {
        this.$router.push(`/user/${ this.account }/blog/intro`);
      } else {
        this.message = "账号或密码错误";
        this.msgType = "danger";
        this.msgIconType = "exclamation-triangle"
      }
    }
  },
  computed: {
    messageType() {
      return `alert-${this.msgType}`;
    }
  }
};
</script>

<style scoped>
.main {
  width: 100%;
  height: 100%;
  border-radius: 20px;
  background: #fff;
  color: #34495e;
  font-size: 20px;
}
.message {
  position: relative;
  margin: 0 auto;
  padding: 5px;
  width: 90%;
  color: #fff;
  background: #e67e22;
}

.vertical-align {
  position: relative;
  top: 50%;
  transform: translate(0, -50%);
}

.login-btn {
  margin-top: 10px;
  width: 70%;
  height: 42px;
  border-radius: 5px;
  background: #16a085;
  color: #fff;
  font-size: 20px;
}
</style>


