<template>
  <div :class="className">
    <div class="name">
      <BaseIcon :type="iconType"></BaseIcon>
      {{ name }}
    </div>
    <input class="input" :type="inputType" @input="$emit('input', $event.target.value)">
  </div>
</template>

<script>
import BaseIcon from "@/components/Icon/BaseIcon";
export default {
  components: {
    BaseIcon
  },
  props: {
    className: String,
    name: String,
    text: String,
    type: {
      type: String,
      validator: value => {
        return ["password", "account"].indexOf(value) !== -1;
      }
    },
    iconType: String
  },
  computed: {
    icon() {
      return `icon-${this.iconType}`;
    },
    inputType() {
      return this.type == "password" ? "password" : "text";
    }
  }
};
</script>

<style scoped>
.input {
  margin: 10px 0 20px 0;
  width: 70%;
  height: 42px;
  border: 1px solid #ccc;
  outline: none;
  color: #34495e;
  font-size: 20px;
  text-indent: 0.3em;
  box-sizing: border-box;
}
</style>


