<template>
  <div class="item" @click="click">{{ name }}</div>
</template>

<script>
export default {
  props: {
    name: String,
    url: String
  },
  methods: {
    click() {
      this.$router.push(this.url)
    }
  }
};
</script>

<style scoped>
.item {
  padding: 5px;
  border-radius: 5px;
}
.item:hover {
  background: #1abc9c;
}
</style>


