<template>
  <i :class="'fa'+ bold + ' fa-' + type + ' fa-' + size + 'x'" @click="click"></i>
</template>

<script>
export default {
  props: {
    type: {
      type: String,
      default: "home"
    },
    size: {
      type: String,
      default: "1"
    },
    canBold: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      isBold: false
    };
  },
  created() {
    if (this.canBold) this.isBold = true;
  },
  methods: {
    click() {
      if (this.canBold) {
        this.isBold = !this.isBold;
        this.$emit("click", !this.isBold);
      } else {
        this.$emit("click");
      }
    }
  },
  computed: {
    bold() {
      if (this.isBold) {
        return "r";
      } else {
        return "";
      }
    }
  }
};
</script>

<style scoped>
.fa {
  cursor: pointer;
}
</style>



