<template>
  <div class="nav">
    <BaseIcon class="icon" type="chevron-circle-left" size="2" @click="$router.go(-1)"></BaseIcon>
    <BaseIcon class="icon" type="heart" size="2" :canBold="true" @click="click"></BaseIcon>
    <AnimItem>
      <div class="info">{{ info }}</div>
    </AnimItem>
  </div>
</template>

<script>
import AnimItem from "@/components/Anim/AnimItem";
import BaseIcon from "@/components/Icon/BaseIcon";

export default {
  components: { AnimItem, BaseIcon },
  data() {
    return {
      info: ""
    };
  },
  methods: {
    click(like) {
      if (like) {
        let vm = this;
        vm.info = "开心!";
      } else {
        this.info = "没那么开心辽";
      }
    }
  }
};
</script>

<style scoped>
.nav {
  padding: 10px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.info {
  line-height: 40px;
}
.icon {
  margin-right: 10px;
}
</style>


