import Vue from "vue"
import Router from "vue-router"
import BlogCover from "@/components/BlogCover"
import Login from "@/components/Login/Login"
import Blog from "@/components/Blog/Blog"
import BlogIntro from "@/components/Blog/BlogIntro"
import BlogIndex from "@/components/Blog/BlogIndex"
import BlogPage from "@/components/Blog/Page/Page"

Vue.use(Router)

export default new Router({
	routes: [
		{
			path: "",
			name: "cover",
			component: BlogCover
		},
		{
			path: "/login",
			name: "login",
			component: Login
		},
		{
			path: "/user/:id/blog/",
			name: "blog",
			component: Blog,
			children: [
				{
					path: "intro",
					component: BlogIntro
				},
				{
					path: "index",
					component: BlogIndex
				},
				{
					path: "post/:postTitle",
					component: BlogPage
				}
			]
		}
	]
})



