<template>
  <div class="wrap">
    <div class="content" v-html="content"></div>
  </div>
</template>

<script>
require("markdown.css/markdown.css");
import axios from "axios";
import marked from "marked";

export default {
  props: {
    title: String
  },
  data() {
    return {
      content: ""
    };
  },
  created() {
    let mdFilePath = `./static/resource/${this.title}.md`;
    axios
      .get(mdFilePath)
      .then(data => {this.content = marked(data.data, { sanitize: true })})
      .catch(err => this.content = "文件不见了");
  }
};
</script>

<style scoped>
.wrap {
  position: absolute;
  top: 70px;
  bottom: 0;
}
.content {
  height: 100%;
  text-align: left;
  overflow: auto;
}
</style>


