<template>
  <div class="cover" @click="login">
    <div class="blog-title vertical-align">{{ name }}</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      name: "LuvBlog",
      coverImageSrc: "src/assets/logo.png"
    };
  },
  methods: {
    login() {
      this.$router.push('login')
    }
  }
};
</script>

<style scoped>
.cover {
  height: 100%;
}
.blog-title {
  font-size: 60px;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
}
.vertical-align {
  position: relative;
  top: 50%;
  transform: translate(0, -50%);
}
</style>


