<template>
  <div class="wrapper">
    <div id="app">
      <router-view/>
    </div>
  </div>
</template>

<script>
export default {
  name: "App"
};
</script>

<style>
html,
body {
  width: 100%;
  height: 100%;
}
.wrapper {
  margin: 0 auto;
  width: 400px;
  height: 600px;
  padding: 20px;
  border-radius: 20px;
  background: #1abc9c;
  box-shadow: 0px 0px 10px #ccc;
}
#app {
  height: 100%;
  color: #fff;
  text-align: center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media screen and (max-width: 400px) {
  .wrapper {
    width: 100%;
    height: 100%;
    border-radius: 0;
  }
}
</style>



