<template>
    <button class="login-btn" @click="$emit('submit')">{{ btnMsg }}</button>
</template>

<script>
export default {
    props: {
        btnMsg: String
    }
}
</script>

<style>
    .login-btn {
        margin-top: 10px;
        width: 70%;
        height: 42px;
        border: 1px solid transparent;
        border-radius: 5px;
        background: #16a085;
        color: #fff;
        font-size: 20px;
    }
</style>


