<template>
  <div class="intro">
    <div>{{intro}}</div>
    <BaseIcon class="enter-btn" type="angle-double-right" size="2" @click="enterBlog"></BaseIcon>
  </div>
</template>

<script>
import BaseIcon from "../Icon/BaseIcon";
export default {
  components: { BaseIcon },
  data() {
    return {
      intro: `
            这个博客记录了李子园和西西的点点滴滴,李子园希望可以一直永远地记录下去
            `
    };
  },
  methods: {
    enterBlog() {
      this.$router.push(`/user/${ this.$route.params.id }/blog/index`);
    }
  }
};
</script>

<style scoped>
.intro {
  padding: 20px;
}
.enter-btn {
  margin-top: 20px;
}
</style>


